import { useTranslation } from "react-i18next";
import style from "./Income.module.css";
import dayjs from "dayjs";
import { localTimeFormat } from "../../../Utils/Functions/localTime";
const IncomeCard = ({ income, setIncomeId }) => {
  const { t } = useTranslation();
  const onDetail = () => {
    setIncomeId(income?._id);
  };

  // console.info("skfdhjsnjk", income);
  return (
    <div onClick={onDetail} className={style.card_container}>
      <div
        className={style.card_container_inner}
        style={{
          width: "50%",
        }}
      >
        <p>{t("recived_from")}</p>
        <h6>
          {income?.incomeFrom
            ? income?.incomeFrom
            : income?.incomeFromUser?.userLegalName ?? ""}
        </h6>
      </div>
      <div
        className={style.card_container_inner}
        style={{
          width: "27%",
        }}
      >
        <p>{t("transaction_id")}</p>
        <h6>N/A</h6>
        <p>
          {" "}
          {dayjs(income?.createdAt).format("DD MMM YYYY")}
          {", "}
          {localTimeFormat(income?.createdAt, "LT")}
        </p>
      </div>
      <div
        className={style.card_container_inner}
        style={{
          width: "15%",
        }}
      >
        <p>
          {t("invoice_no")} {income?.invoice_number ?? ""}
        </p>
        <h6>
          {t("rs_only")} {income?.incomeAmount ?? ""}
        </h6>
        <p>{income?.incomeAccount ?? ""}</p>
        <h6></h6>
      </div>
    </div>
  );
};

export default IncomeCard;
